.Section_text_p1{
    text-align: center;
}
.title{
    font-size: 3rem;
    text-align: center;
    color: yellow;
}
.about-containers{
    gap: 3rem;
    display: grid;
    margin-bottom: 2rem;
    margin-top: 2rem;
    width: 700px;
    justify-content: center;
    
}
.tick{
    border: none;
    color:yellow;
}
.details-containers{
    padding: 1.5rem;
    flex: 1;
    border-radius: 2rem;
    border: white 0.1rem solid;
    border-color: yellow;
    text-align: center;
}
#experience{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    margin-top: 60px;
}
.experience-sub-title{
    color: yellow;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 2rem;
}
.experience-details-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.article-container{
    display: flex;
    text-align: initial;
    flex-wrap: wrap;
    flex-direction: row;
    gap: 2.5rem;
    justify-content: space-around;
}
article{
    display: flex;
    width: 10rem;
    justify-content: space-around;
    gap: 0.5rem;
}

@media screen and (max-width:720px) {
    .about-containers{
        width: 250px;
    }
}
