*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#projects{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    margin-top: 60px;
}
.available{
    width: 100%;
    padding: 100px 0;
}
h1{
    font-size: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    color:  yellow;
}
#h1span{
    margin-left: 15px;
}
 .av-box{
    width: 95%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 35px;
}
.av-card{
    width: 372px;
    height: 440px;
    padding: 10px;
    margin-bottom: 20px;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    border: 2px solid  yellow;
    border-radius: 20px;
}
.av-image{
    width: 350px;
    height: 250px;
    margin: 0 auto;
    overflow: hidden;
}
 .av-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    transition: 0.3s;
    object-fit: cover;
} 
.av-info h2{
    width: 40%;
    text-align: center;
    margin: 10px auto 10px auto;
    font-size: 25px;
    color:  yellow;
} 

.av-btn{
    display: flex;
    align-items: center;
    background:white;
    justify-content: center;
    color:black;
    margin: 0 80px;
    border-radius: 30px;
    padding: 7px;
    transition: 0.3s;
}.av-btn:hover{
    background:yellow;
}  
a{
    text-decoration: none;
}

@media screen and (max-width:720px) {
    .av-box{
        display: block;
    }
}

