#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}
.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 4rem;
    color: yellow;
    margin-bottom: 2rem;
}
.skillDesc{
    font-weight: 200;
    font-size: 1.10rem;
    max-width: 50rem;
    padding: 0 2rem;
}